import { SingleModelResponse } from "./SingleModelResponse";
import { Table2TableViewModel } from "../ViewModel/Table2TableViewModel";

export class Table2TableResponse extends SingleModelResponse<Table2TableViewModel> {
  Model: Table2TableViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new Table2TableViewModel();
      this.Model.exec_table_seq=obj.Model.exec_table_seq;
      this.Model.exec_group=obj.Model.exec_group;
      this.Model.src_schema=obj && obj.Model.src_schema || null;
      this.Model.src_tablename=obj && obj.Model.src_tablename || null;
      this.Model.schemaname=obj && obj.Model.schemaname || null;
      this.Model.tablename=obj.Model.tablename;
      this.Model.import_mode_key=obj.Model.import_mode_key;
      this.Model.last_data_type_key=obj && obj.Model.last_data_type_key || null;
      this.Model.last_data_column_name=obj && obj.Model.last_data_column_name || null;
      this.Model.exec_seq=obj.Model.exec_seq;
      this.Model.is_active=obj.Model.is_active;
      this.Model.where_condition=obj && obj.Model.where_condition || null;
      this.Model.tran_flag=obj && obj.Model.tran_flag || null;
      this.Model.db_conn_id=obj && obj.Model.db_conn_id || null;
      this.Model.memo=obj && obj.Model.memo || null;
      this.Model.creator=obj.Model.creator;
      this.Model.create_time=obj.Model.create_time;
      this.Model.modifier=obj.Model.modifier;
      this.Model.last_update_time=obj.Model.last_update_time;

      this.Model.import_mode = obj.Model.import_mode;
      this.Model.last_data_type = (obj.Model && obj.Model.last_data_type) || null;
      this.Model.db_conn_string = (obj.Model && obj.Model.db_conn_string) || null;
      this.Model.is_active_desc = obj.Model.is_active_desc;
      this.Model.src_tablefullname=obj.Model.src_schema+"."+obj.Model.src_tablename;
      this.Model.tablefullname=obj.Model.schemaname+"."+obj.Model.tablename;
      switch (obj.Model.is_active) {
        case false: {
          this.Model.is_active_color = "danger";
          break;
        }

        default:
        this.Model.is_active_color = "active";
      }
    }
  }
}



