import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { Table2TableListResponse } from "../../Model/Response/Table2TableListResponse";
import { Table2TableResponse } from "../../Model/Response/Table2TableResponse";
import { StringResponse } from "../../Model/Response/StringResponse";
import { Table2TableViewModel } from '../../Model/ViewModel/Table2TableViewModel';
import { StringViewModel } from "../../Model/ViewModel/StringViewModel";
import { TableSetServicesProvider } from '../table-set-services/table-set-services';

/*
  Generated class for the Table2TableServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class Table2TableServicesProvider extends TableSetServicesProvider {
  private Table2Tables: Table2TableListResponse;
  private Table2Table: Table2TableResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "Table2TableExt";
    console.log("Hello Table2TableServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "Table2Table?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Table2Tables = new Table2TableListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Table2Tables;
    });
  }



  public PostPutAsync(Table2TableViewModel:Table2TableViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "Table2Table";
    else
    sub_url = "Table2Table/{1}";
    sub_url = String.Format(sub_url, this.ctl,Table2TableViewModel.exec_table_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,Table2TableViewModel,mode).map(
      item => {
        this.Table2Table = new Table2TableResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.Table2Table;
      }
    );
  }
  public GetMergeSqlAsync(src_schemaname:string,src_tablename:string,where_condition:string,schemaname:string,tablename:string,import_mode_key:string,last_data_type_key:string, last_data_column_name:string , exec_group:string ) {
    var sub_url: string;
    sub_url = "Table2Table/get_merge_sql?src_schemaname={1}&src_tablename={2}&where_condition={3}&schemaname={4}&tablename={5}&import_mode_key={6}&last_data_type_key={7}&last_data_column_name={8}&exec_group={9}";
    sub_url = String.Format(sub_url, this.ctl,src_schemaname, src_tablename, where_condition, schemaname, tablename, import_mode_key, last_data_type_key, last_data_column_name, exec_group);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
  public DeleteAsync(exec_table_seq:number) {
    var sub_url: string;
    sub_url = "Table2Table/{1}";
    sub_url = String.Format(sub_url, this.ctl,exec_table_seq);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: ""
        });
      }
    );
  }
  public ExecuteAsync(StringViewModel:StringViewModel) {
    var sub_url: string;
    sub_url = "Table2Table/Execute";
    return this.Services.PostAsync(sub_url, this.ctl, true,StringViewModel).map(
      item => {
        this.Table2Table = new Table2TableResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.Table2Table;
      }
    );
  }
}



